body {
  margin: 0 3vw;
  background-color: #F9F9F9;
  padding: 0;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.project-container{
  margin: 80px 15vw;
  width: 1200px;

  .project-content{
    width: 100%;
    display: inline-flex;
    flex-direction: row;
    flex-wrap: wrap;

    .content-section{
      width: calc(50% - 40px);
      min-height: calc(500px - 40px);
      padding: 20px;
      display: flex;
      flex-direction: column;
      justify-content: center;

      img{
        width: 100%;
        max-height: 400px;
        align-self: center;
        object-fit: contain;
      }

      ul{
        font-family: $main-font;
        font-weight: 300;
        font-size: 18px;
      }
    }
  }
}

#main {
  margin-top: 6vw;
  &>h6{
    margin-top: 80px;
    display: flex;
    justify-content: center;
  }
}

#LearningOutcomes{
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-top: 250px;
}

#projects > div,
#social {
  margin: 250px 0px;
}


@media only screen and (max-width: 600px) {
  //phones
  .project-container{
    width: 100%;
    margin: 80px 5vw;
    .project-content{
      .content-section{
        min-height: auto;
        width: calc(100% - 80px);
      }
    }
  }

  #main {
    margin-top: 6vw;
    &>p{
      font-size: 10vw;
    }
    &>h6{
      font-size: 4vw;
      margin-top: 30px;
    }
  }

  #projects > div,
  #social {
    &>h1{
      font-size: 9vw;
    }
    &>p{
      font-size: 8vw;
    }
    margin: 20px 0px;
  }
}

@media only screen and (max-width: 1024px) and (min-width: 768px) {
  //tablets
  .project-container{
    width: 100%;
    margin: 80px 5vw;
    .project-content{
      .content-section{
        min-height: auto;
        width: calc(100% - 40px);
      }
    }
  }

  #main {
    margin-top: 6vw;
    &>p{
      font-size: 10vw;
    }
    &>h6{
      font-size: 4vw;
      margin-top: 15px;
    }
  }

  #projects > div,
  #social {
    &>h1{
      font-size: 9vw;
    }
    &>p{
      font-size: 8vw;
    }
    margin: 20px 0px;
  }
}
